export default {
	props: {
		item: {
			type: Object
		},
		index: {
			type: Number
		}
	},
	methods: {
		openSpace() {
			console.log('打开个人空间')
		},
		openDetail() {
			console.log('打开详情页')
		},
		doSupport(type) {
			this.$emit('doSupport', {
				type,
				index: this.index
			})
		},
		fllow () {
			this.$emit('fllow', this.index)
		},
	}
}
